.four-oh-four {
  background: url(#{$path}/images/404.jpg) no-repeat left bottom;
  background-size: cover;
  padding: 0 !important;

  @media (min-resolution: 120dpi) and (max-width: map-get($breakpoints, 'small--down')) {
    background-image: url(#{$path}/images/404@2x.jpg);
  }

  @include respond-to('small') {
    background-image: url(#{$path}/images/404--small.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'small')) {
    background-image: url(#{$path}/images/404--small@2x.jpg);
  }

  @include respond-to('large') {
    background-image: url(#{$path}/images/404--large.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'large')) {
    background-image: url(#{$path}/images/404--large@2x.jpg);
  }

  @include respond-to('x-large') {
    background-image: url(#{$path}/images/404--x-large.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'x-large')) {
    background-image: url(#{$path}/images/404--x-large@2x.jpg);
  }
}

.four-oh-four__content {
  align-items: flex-end;
  display: flex;
  height: calc(100vh - #{unit(4)});
  justify-content: center;
  max-height: 88rem;
  min-height: 40rem;

  @include respond-to('large') {
    height: 48vw;
  }
}

.four-oh-four__content__inner {
  @include smooth-text;
  @include null-margins;
  color: $color--white;
  padding: 0 unit(1) unit(1);
  text-align: center;
  width: 100%;

  @include respond-to('small') {
    padding: 0 unit(4) unit(2);
  }
}

.four-oh-four__lede {
  border-top: 1px solid rgba($color--white, 0.1);
  padding-top: unit(1);
}
